.container {
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 10px;
    margin-left: 40px;
    margin-right: 40px;
}

.container input {
    width: 80%;
    height: 8%;
    font-size: larger;
    font-weight: 400;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 20px;
    color: rgb(87, 86, 86);
    border: 1px solid lightgray;
    background-color: rgb(246, 249, 249);

}

.container textarea {
    width: 80%;
    height: 70%;
    font-size: large;
    padding: 10px;
    color: rgb(87, 86, 86);
    border: 1px solid lightgray;
    background-color: rgb(246, 249, 249);

}

.container button {
    min-width: 60px;
    width: 20%;
    height: 30px;
    font-size: medium;
    margin-top: 20px;
    border: none;
    border-radius: 5px;
    color: white;
    font-weight: 500;
    background-color: rgb(145, 197, 214);
}

.container button:hover {
    background-color: rgb(119, 183, 204);
}

.container input:focus, .container textarea:focus {
    outline: 1px solid lightgray;
}